html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

html {
    font-size: 62.5%;
    height: 100vh;
    border-left: 4px solid black;
    border-right: 4px solid black;
    background: lightgrey;
}


a, button {
    text-decoration: none;
    color: black;
}

#nav-bar {
    font-family: Georgia, "Times New Roman", Times, serif;
    font-size: 2.4rem;
    padding: 1.5rem;
    display: flex;
    flex-direction: row;
    width: 40%;
}

#nav-bar a {
    text-decoration: none;
    background: khaki;
    padding: 1rem 2rem;
    border: 1px solid black;
}

#nav-bar a:hover {
    transition-duration: 0.5s;
    background: white;
}

#nav-bar #here {
    background: white;
}

#upload-your-image {
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 3.6rem;
    text-align: center;
}

#form {
    margin-top: 2rem;
    text-align: center;
}

#image-submit-button, #options-button {
    margin-top: 2rem;
    cursor: pointer;
    font-size: 1.8rem;
    border-radius: 4px;
    box-shadow: 0;
}

#image-submit-button {
    background: rgb(35, 255, 108);
    border: 2px solid rgb(18, 184, 18);
    margin-right: 2rem;
}

#image-submit-button:hover {
    background:white;
    color: rgb(35, 255, 108);
    border: 2px solid rgb(35, 255, 108);
    transition: 0.25s;
}

#options-button {
    border: 2px solid black;
}

#options-button:hover {
    background: white;
    color: darkgray;
    border: 2px solid darkgray;
    transition: 0.25s;
}

#options {
  margin: 2rem auto 0 auto;
}

.option {
  margin: 0 auto;
  display: flex;
  justify-content: center;
}

.options-label {
  font-size: 1.8rem;
  margin-right: 1rem;
}

#manifesto {
  width: 50rem;
  height: 15rem;
  font-family: 'Times New Roman', Times, serif;
  font-size: 1.4rem;
}

#upload-error-message {
  text-align: center;
  color: red;
  font-size: 1.8rem;
  margin-top: 0.5rem;
}

#upload-message {
  text-align: center;
  color: black;
  font-size: 1.8rem;
  margin-top: 0.5rem;
}

.black-text {
  color: black;
}

.red-text {
  color: red
}

#man-count {
  font-size: 1.4rem;
  margin-left: 0.5rem;
}

#recaptcha-home {
  display: block;
  margin: 1rem auto;
  max-width: max-content;
}

.rc-anchor-light {
  margin: 1rem auto;
  max-width: 95%;
}

